<template>
    <div class="body">
        <div class="header">
            <van-nav-bar fixed placeholder z-index="999" title="标题" left-text="返回" @click-left="goBack" left-arrow>
                <van-icon name="search" slot="right" />
            </van-nav-bar>
            <van-cell-group>
            <van-field
                v-model="classname"
                center
                required
                clearable
                label="类别名称"
                placeholder="请输入类别名称"
            >
                <van-button @click="addItemClass" slot="button" size="small" type="primary">新增类别</van-button>
            </van-field>
            <van-field
                v-model="comment"
                center
                clearable
                label="备注"
                placeholder="请输入类别备注"
            >
            </van-field>
            </van-cell-group>
        </div>
        <van-divider>类别列表-左滑删除</van-divider>
        <div class="main">
            <van-swipe-cell v-for="cs in classes" :key="cs.cid" :right-width="65">
                <van-cell :title="cs.classname" :value="cs.comment" />
                <template #right>
                    <van-button @click="delItemClassify(cs.cid)" square text="删除" type="danger"
                        class="delete-button" />
                </template>
            </van-swipe-cell>
        </div>
        <div class="footer"></div>
    </div>
</template>
<style>
    .body{
        min-height: 100vh;
        background-color: #f1f5f7;
    }
    .heade{

    }
    .main{

    }
    .footer{

    }
</style>
<script>
import axios from 'axios';
import { Toast } from 'vant';

    const options = {
        data(){
            return {
                classname: "",
                comment:"",
                classes:[],
            }
        },
        methods:{
            goBack(){
                this.$router.push("/manage")
            },
            addItemClass(){
                if (!this.classname) {
                    Toast("类名不能为空!");
                    return;
                }
                const ItemClassify = {
                    classname: this.classname,
                    comment: this.comment
                };
                axios.post("/admin/additemclass",ItemClassify).then((resp)=>{
                    Toast(resp.data.msg);
                    this.getItemClasses();
                });
            },
            getItemClasses(){
                axios.get("/admin/itemclasses").then((resp)=>{
                    this.classes = resp.data.data;
                });
            },
            delItemClassify(cid){
                let form = new FormData();
                form.set("cid",cid);
                axios.post("/admin/delitemclass",form).then((resp)=>{
                    Toast(resp.data.msg);
                    if (resp.data.data) {
                        this.getItemClasses();
                    }
                });
            }
        },
        mounted(){
            this.getItemClasses();
        }
    };

    export default options;
</script>